import React from 'react'
import Link from 'gatsby-link'

import MagicBox from '../components/MagicBox'

const PresentationPage = () => (
  <div>
    <MagicBox />
    <br />
    <Link to="/">Go back to the homepage</Link>
  </div>
)

export default PresentationPage



